import "./App.css";
import * as React from "react";
import {DeviceType, Room, RoomState, RoomWhiteboard, WhiteWebSdk, WhiteWebSdkConfiguration} from "white-web-sdk";
import * as dsBridge from "dsbridge";
import isTouchDevice from "is-touch-device";
import {GlobalState, ImageInformation, MemberState, PptPage, TextareaBox, ViewMode} from "white-appliances";
import {AkkoEvent} from "akko";


export type AppState = {
    room?: Room;
};

type EventEntry = {
    eventName: string;
    payload: any;
}

export class App extends React.Component<{}, AppState> {

    private whiteWebSdk: WhiteWebSdk;
    private room: Room;

    // [注意]
    // miniToken 不应该存储在前端，也不应该通过网络传输。
    // 它应该仅由业务服务器持有，并使用它调用 White 的 API。
    // 此处仅为了方便演示，将它写在前端代码中。
    // private static readonly miniToken = "";

    public constructor(props: {}) {
        super(props);
        // this.whiteWebSdk = new WhiteWebSdk({
        //     deviceType: isTouchDevice() ? DeviceType.Touch : DeviceType.Desktop,
        //     zoomMinScale: 0.1,
        //     zoomMaxScale: 10,
        // });
        this.state = {};
        dsBridge.register("addValue", function (l: number, r: number) {
            return l + r;
        });

        dsBridge.registerAsyn("sdk", {
            tag: "sdk",
            newWhiteSdk: (config: string) => {
                const parsed: WhiteWebSdkConfiguration = JSON.parse(config) as WhiteWebSdkConfiguration;
                this.whiteWebSdk = new WhiteWebSdk({
                    // FIXME - 怎么把 Java 枚举转换成 JS 枚举
                    deviceType: isTouchDevice() ? DeviceType.Touch : DeviceType.Desktop,
                    zoomMinScale: parsed.zoomMinScale,
                    zoomMaxScale: parsed.zoomMaxScale,
                });
                // alert(this.whiteWebSdk);
            },
            joinRoom: (uuid: string, roomToken: string, responseCallback: any) => {
                this.whiteWebSdk.joinRoom({uuid, roomToken}, {
                    onRoomStateChanged: modifyState => {
                        dsBridge.call("sdk.fireRoomStateChanged", JSON.stringify(modifyState));
                    },
                    onPhaseChanged: phase => {
                        dsBridge.call("sdk.firePhaseChanged", phase);
                        console.log(`room ${uuid} changed: ${phase}`);
                    },
                    onDisconnectWithError: error => {
                        dsBridge.call("sdk.fireDisconnectWithError", error.message);
                        console.error(error);
                    },
                    onKickedWithReason: reason => {
                        dsBridge.call("sdk.fireKickedWithReason", reason);
                        console.error("kicked with reason: " + reason);
                    },
                    onBeingAbleToCommitChange: isAbleToCommit => {
                        dsBridge.call("sdk.fireBeingAbleToCommitChange", isAbleToCommit);
                    },
                    onCatchErrorWhenAppendFrame: (userId: number, error: Error) => {
                        dsBridge.call("sdk.fireCatchErrorWhenAppendFrame", {userId: userId, error: error.message});
                    }
                }).then((room: Room) => {
                    this.setState({
                        room: room,
                    });
                    this.room = room;
                    const state: RoomState = room.state;
                    return responseCallback(JSON.stringify({state}));
                }).catch((e: Error) => {
                    return responseCallback(JSON.stringify({__error: {message: e.message, jsStack: e.stack}}));
                });
            }
        });

        dsBridge.registerAsyn("room", {
            tag: "room",
            setGlobalState: (modifyState: string) => {
                const parsed: Partial<GlobalState> = JSON.parse(modifyState) as Partial<GlobalState>;
                this.room.setGlobalState(parsed);
            },
            setMemberState: (memberState: string) => {
                const parsed: Partial<MemberState> = JSON.parse(memberState) as Partial<MemberState>;
                this.room.setMemberState(parsed);
            },
            setViewMode: (viewMode: string) => {
                const parsed: Partial<ViewMode> = JSON.parse(viewMode) as Partial<ViewMode>;
                this.room.setViewMode(parsed);
            },
            setViewSize: (width: number, height: number) => {
                this.room.setViewSize(width, height);
            },
            disconnect: async () => {
                await this.room.disconnect();
            },
            updateTextarea: (textareaBox: string) => {
                const parsed: TextareaBox = JSON.parse(textareaBox) as TextareaBox;
                this.room.updateTextarea(parsed);
            },
            insertNewPage: (index: number) => {
                this.room.insertNewPage(index);
            },
            removePage: (index: number) => {
                this.room.removePage(index);
            },
            insertImage: (imageInfo: string) => {
                const parsed: ImageInformation = JSON.parse(imageInfo) as ImageInformation;
                this.room.insertImage(parsed);
            },
            pushPptPages: (pages: string) => {
                const parsed: PptPage[] = JSON.parse(pages) as PptPage[];
                this.room.pushPptPages(parsed);
            },
            completeImageUpload: (uuid: string, url: string) => {
                this.room.completeImageUpload(uuid, url);
            },
            getMemberState: (responseCallback: any) => {
                return responseCallback(JSON.stringify(this.room.state.memberState));
            },
            getGlobalState: (responseCallback: any) => {
                return responseCallback(JSON.stringify(this.room.state.globalState));
            },
            getRoomMembers: (responseCallback: any) => {
                return responseCallback(JSON.stringify(this.room.state.roomMembers));
            },
            getPptImages: (responseCallback: any) => {
                return responseCallback(JSON.stringify(this.room.state.pptImages));
            },
            getZoomScale: (responseCallback: any) => {
                return responseCallback(JSON.stringify(this.room.state.zoomScale));
            },
            getBroadcastState: (responseCallback: any) => {
                return responseCallback(JSON.stringify(this.room.state.broadcastState));
            },
            dispatchMagixEvent: (event: any) => {

                const eventEntry: EventEntry = JSON.parse(event);
                this.room.dispatchMagixEvent(eventEntry.eventName, eventEntry.payload);
            },
            // waitMagixEvent: (filter: EventFilter) => {
            // },
            addMagixEventListener: (event: string) => {
                this.room.addMagixEventListener(event, (evt: AkkoEvent) => {
                    const uuid: string = this.room.uuid;
                    dsBridge.call("sdk.fireMagixEvent", JSON.stringify({
                        uuid: uuid,
                        eventName: evt.event,
                        payload: evt.payload,
                        scope: evt.scope,
                        authorId: evt.authorId,
                    }));
                });
            },
            removeMagixEventListener: (event: string) => {
                this.room.removeMagixEventListener(event);
            },
            convertToPointInWorld: (x: number, y: number, responseCallback: any) => {
                const point: { x: number, y: number } = this.room.convertToPointInWorld({x, y});
                return responseCallback(JSON.stringify(point));
            }
        });

    }

    public async componentWillMount(): Promise<void> {
    }

    public render(): React.ReactNode {
        if (!this.state.room) {
            // 在成功加入房间之前无法获取 room 对象，此时无法显示白板。
            return <div>is joining room...</div>;
        } else {
            return (
                <div className="whiteboard-container">
                    <RoomWhiteboard room={this.state.room}/>
                </div>
            );
        }
    }
}

export default App;



